import React, { useState } from "react"
import * as S from "./styled"
import ModalMenu from "../ModalMenu"

const Topbar = () => {
  if (typeof window !== `undefined`) {
    require("mailgo")
  }

  const [isOpen, setIsOpen] = useState(false)

  return (
    <>
      <S.TopbarWrapper>
        <S.MenuText>
          <span onClick={e => setIsOpen(isOpen ? false : true)}>Menu</span>
        </S.MenuText>
        <S.TitleText>
          <h1>Guilherme</h1>
        </S.TitleText>
        <S.EmailText>
          <a href="mailto:guilhermeorcezi@gmail.com">Email</a>
        </S.EmailText>
      </S.TopbarWrapper>
      {isOpen && <ModalMenu />}
    </>
  )
}
export default Topbar
