import styled from "styled-components"

export const TopbarWrapper = styled.div`
  font-size: 13px;
  padding-left: 24px;
  padding-right: 24px;
  -webkit-box-align: center;
  align-items: center;
  display: flex;
  flex-direction: row;
  -webkit-box-pack: justify;
  justify-content: space-between;
  left: 0px;
  line-height: 1em;
  position: fixed;
  right: 0px;
  top: 0px;
  z-index: 500;
  padding-top: 12px;
  background-color: rgb(52, 52, 52);
  box-shadow: rgba(0, 0, 0, 0.173) 0px 5px 0px;
  transition: background-color 0.3s ease 0s, box-shadow 0.3s ease 0s,
    padding 0.5s ease 0s;

  @media screen and (min-width: 320px) {
    padding-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
    padding-right: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-top: calc(12px + (4 * (100vw - 320px)) / 1120);
    padding-left: calc(24px + (8 * (100vw - 320px)) / 1120);
    font-size: calc(13px + (2 * (100vw - 320px)) / 1120);
  }

  @media (min-width: 768px) {
    display: none;
  }

  div {
    flex: 0 0 33.3333%;
  }

  div:nth-child(1) {
    text-align: left;
  }
  div:nth-child(2) {
    text-align: center;
  }
  div:nth-child(3) {
    text-align: right;
  }

  @media screen and (min-width: 320px) {
    a {
      padding-bottom: calc(3px + (1 * (100vw - 320px)) / 1120);
      padding-top: calc(3px + (1 * (100vw - 320px)) / 1120);
    }
  }
`

export const MenuText = styled.div`
  a,
  span {
    padding-top: 10px;
    font-weight: 500;
    letter-spacing: 0.08em;
    line-height: 1.4em;
    text-align: center;
    text-transform: uppercase;
    color: rgb(237, 237, 237);
    font-family: Rubik, Arial, sans-serif;
    border-style: solid;
    border-width: 0px 0px 2px;
    transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
    border-color: transparent;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    border-style: solid;
    text-decoration: none;
    transition: border-color 0.1s ease 0s, color 0.1s ease 0s;
  }

  a:hover,
  span:hover {
    color: rgb(237, 237, 237) !important;
    border-color: rgb(237, 237, 237);
  }
`

export const TitleText = styled.div`
  h1 {
    color: rgb(237, 237, 237);
    font-family: Rubik, Arial, sans-serif;
    white-space: normal;
    border-style: solid;
    border-width: 0px 0px 2px;
    transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
    border-color: transparent;
    font-size: 22px;
    font-weight: bolder;
  }
`

export const EmailText = styled.div`
  a {
    padding-top: 3px;
    font-weight: 500;
    letter-spacing: 0.08em;
    line-height: 1.4em;
    text-transform: uppercase;
    color: rgb(237, 237, 237);
    font-family: Rubik, Arial, sans-serif;
    border-style: solid;
    border-width: 0px 0px 2px;
    transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
    border-color: transparent;
    cursor: pointer;
    text-decoration: none;
  }

  a:hover {
    color: rgb(237, 237, 237) !important;
    border-color: rgb(237, 237, 237);
  }
`
export const MenuModal = styled.menu`
  -webkit-box-align: center;
  align-items: center;
  background-color: rgb(52, 52, 52);
  bottom: 0px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  left: 0px;
  position: fixed;
  right: 0px;
  text-align: center;
  top: 0px;
  z-index: 300;
  animation: 0.1s linear 0s 1 normal none running jBcSpD;

  @media (min-width: 768px) {
    display: none;
  }
`
export const NavWrapper = styled.nav``

export const ListWrapper = styled.ul``

export const ListItem = styled.li`
  margin: 0.8vh 0px;

  a,
  button {
    color: rgb(158, 158, 158);
    font-size: 14px;
    line-height: 1.6em;
    padding-top: 3px;
    font-weight: 500;
    letter-spacing: 0.08em;
    line-height: 1.4em;
    text-align: center;
    text-transform: uppercase;
    font-family: Rubik, Arial, sans-serif;
    border-style: solid;
    border-width: 0px 0px 2px;
    transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
    border-color: transparent;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    background-color: transparent;
    border-style: solid;
    text-decoration: none;
    transition: border-color 0.1s ease 0s, color 0.1s ease 0s;
  }

  a:hover,
  button {
    color: rgb(237, 237, 237) !important;
    border-color: rgb(237, 237, 237);
  }

  @media screen and (min-width: 320px) {
    a,
    button {
      font-size: calc(14px + (2 * (100vw - 320px)) / 1120);
      padding-bottom: calc(3px + (1 * (100vw - 320px)) / 1120);
      padding-top: calc(3px + (1 * (100vw - 320px)) / 1120);
    }
  }
`
