import React from "react"
import * as S from "./styled"
import { FaGithub, FaLinkedin, FaDev } from "react-icons/fa"
import {TiHeartOutline} from "react-icons/ti"

const Footer = () => (
  <S.FooterWrapper>
    <S.TextBlock>
      <h1>Want to contact me?</h1>
      <p>
        For business enquiries or casual chatter drop me a line (+55 12
        996158865). 
      </p>
    </S.TextBlock>
    <S.Separator />
    <S.linkEmail href="mailto:guilhermeorcezi@gmail.com">
      guilhermeorcezi@gmail.com
    </S.linkEmail>
    <S.Separator />
    <S.SocialLinks>
      <S.LinkItem>
        <a
          href="https://github.com/guilhermeorcezi"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="github"
        >
          <FaGithub />
        </a>
      </S.LinkItem>
      <S.LinkItem>
        <a
          href="https://www.linkedin.com/in/guilherme-orcezi/"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="linkedin"
        >
          <FaLinkedin />
        </a>
      </S.LinkItem>
      <S.LinkItem>
        <a
          href="https://dev.to/guilhermeorcezi"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="dev"
        >
          <FaDev />
        </a>
      </S.LinkItem>
    </S.SocialLinks>
    <S.AuthorCredit>
    Created by Guilherme Orcezi with <TiHeartOutline/>
    </S.AuthorCredit>
  </S.FooterWrapper>
)

export default Footer
