import styled from "styled-components"

export const ContactWrapper = styled.div`
  display: flex;
  flex-direction: column;
  -webkit-box-pack: end;
  justify-content: flex-end;
  flex: 0 0 25%;
`

export const TextString = styled.p`
  font-size: 13px;
  line-height: 1.6em;
  font-family: Rubik, Arial, sans-serif;

  @media screen and (min-width: 320px) {
    font-size: calc(13px + (2 * (100vw - 320px)) / 1120);
  }
`

export const TextSeparator = styled.hr`
  height: 0px;
  clear: both;
  position: relative;
  width: 100%;
  margin-top: 6px;
  margin-bottom: 6px;
  border-color: transparent;
  border-width: 0px 0px 2px;
  background: transparent;

  @media screen and (min-width: 320px) {
    margin-bottom: calc(6px + (2 * (100vw - 320px)) / 1120);
    margin-top: calc(6px + (2 * (100vw - 320px)) / 1120);
  }
`

export const SocialLinks = styled.ul`
  @media screen and (min-width: 320px) {
    margin-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
  }
`

export const LinkItem = styled.li`
  margin-left: 6px;
  margin-right: 6px;
  display: inline;

  @media screen and (min-width: 320px) {
    li:first-child {
      margin-left: calc(0px + (0 * (100vw - 320px)) / 1120);
      margin-left: 0px;
    }
  }

  a{
    color: rgb(237, 237, 237) !important;
    font-size:18px;
  }
`
