import React from "react"
import * as S from "./styled"
import { FaGithub, FaLinkedin, FaDev } from "react-icons/fa"
import {TiHeartOutline} from "react-icons/ti"

const Contact = () => (
  <S.ContactWrapper>
    <S.TextString>
      For business enquiries: guilhermeorcezi@gmail.com. Created by Guilherme Orcezi with <TiHeartOutline/>
    </S.TextString>
    <S.TextSeparator />
    <S.SocialLinks>
      <S.LinkItem>
        <a
          href="https://github.com/guilhermeorcezi"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="github"
        >
          <FaGithub />
        </a>
      </S.LinkItem>
      <S.LinkItem>
        <a
          href="https://www.linkedin.com/in/guilherme-orcezi/"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="linkedin"
        >
          <FaLinkedin />
        </a>
      </S.LinkItem>
      <S.LinkItem>
        <a
          href="https://dev.to/guilhermeorcezi"
          target="_blank"
          rel="noopener noreferrer"
          aria-label="dev"
        >
          <FaDev />
        </a>
      </S.LinkItem>
    </S.SocialLinks>
  </S.ContactWrapper>
)

export default Contact
