import styled from "styled-components"

export const MenuModal = styled.menu`
  -webkit-box-align: center;
  align-items: center;
  background-color: rgb(52, 52, 52);
  bottom: 0px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;
  left: 0px;
  position: fixed;
  right: 0px;
  text-align: center;
  top: 0px;
  z-index: 300;
  animation: 0.1s linear 0s 1 normal none running jBcSpD;

  @media (min-width: 768px) {
    display: none;
  }
`
export const NavWrapper = styled.nav``

export const ListWrapper = styled.ul``

export const ListItem = styled.li`
  margin: 5vh 0px;

  a {
    color: rgb(158, 158, 158);
    font-size: 14px;
    line-height: 1.6em;
    padding-top: 3px;
    font-weight: 500;
    letter-spacing: 0.08em;
    line-height: 1.4em;
    text-align: center;
    text-transform: uppercase;
    font-family: Rubik, Arial, sans-serif;
    border-style: solid;
    border-width: 0px 0px 2px;
    transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
    border-color: transparent;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    border-style: solid;
    text-decoration: none;
    transition: border-color 0.1s ease 0s, color 0.1s ease 0s;
  }

  a:hover {
    color: rgb(237, 237, 237) !important;
    border-color: rgb(237, 237, 237);
  }

  @media screen and (min-width: 320px) {
    a {
      font-size: calc(14px + (2 * (100vw - 320px)) / 1120);
      padding-bottom: calc(3px + (1 * (100vw - 320px)) / 1120);
      padding-top: calc(3px + (1 * (100vw - 320px)) / 1120);
    }
  }
`
