import styled from "styled-components"

export const LayoutMain = styled.main`
  background-color: #191919;
  padding-bottom: 48px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  justify-content: center;

  @media screen and (min-width: 768px) {
    margin-left: 260px;
    min-height: 100vh;
    padding-bottom: 120px;
  }

  @media screen and (min-width: 320px) {
    padding-right: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-left: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-bottom: calc(48px + (16 * (100vw - 320px)) / 1120);
  }
`
export const LayoutWrapper = styled.section`
  @media screen and (min-width: 320px) {
    flex-direction: row;
  }
`

export const MainContent = styled.div`
  max-width: 1280px;
  animation: 0.3s linear 0s 1 normal none running jBcSpD;

  @media (min-width: 768px) @media screen and (min-width: 320px) {
    padding-right: calc(48px + (16 * (100vw - 320px)) / 1120);
    padding-left: calc(48px + (16 * (100vw - 320px)) / 1120);
    margin-bottom: calc(48px + (16 * (100vw - 320px)) / 1120);
    margin-top: calc(48px + (16 * (100vw - 320px)) / 1120);
  }

  @media (min-width: 768px) {
    margin-top: 48px;
    margin-bottom: 48px;
    padding-left: 48px;
    padding-right: 48px;
  }
`
