import React from "react"
import * as S from "./styled"
import { Link } from "gatsby"

const ModalMenu = () => {
  return (
    <S.MenuModal>
      <S.NavWrapper>
        <S.ListWrapper>
          <S.ListItem>
            <Link to="/">Home</Link>
          </S.ListItem>
          <S.ListItem>
            <Link to="/projects">Projects</Link>
          </S.ListItem>
          <S.ListItem>
            <Link to="/articles">Articles</Link>
          </S.ListItem>
          <S.ListItem>
            <a href="mailto:guilhermeorcezi@gmail.com">Contact</a>
          </S.ListItem>
        </S.ListWrapper>
      </S.NavWrapper>
    </S.MenuModal>
  )
}

export default ModalMenu
