import React from "react"
import * as S from "./styled"
import { Link } from "gatsby"

const Home = () => (
  <S.HomeWrapper>
    <S.TextBlock>
      <S.TitleH>
        <span className="span-name">Hi I'm Guilherme Orcezi</span>
        <span className="span-describe">Full Stack Developer Jr</span>
      </S.TitleH>

      <p>
        I'm studying Systems Analysis and Development, interested in both
         frontend and backend. I've been studying a lot about NodeJS and
        React, enjoying everything that wonderful Javascript has to offer, as
        I see a promising future for this stack with React Native on mobile.
      </p>
    </S.TextBlock>
    <S.ActionBar>
      <Link to="/projects" className="actionText">
        See my projects
      </Link>
      <a href="mailto:guilhermeorcezi@gmail.com" className="actionText">
        Contact me
      </a>
    </S.ActionBar>
  </S.HomeWrapper>
)

export default Home
