import React from "react"
import * as S from "./styled"
import { Link } from "gatsby"

const Menu = () => (
  <S.MenuWrapper>
    <S.NavWrapper>
      <S.ListWrapper>
        <S.ListItem>
          <Link to="/" activeClassName="active-menu">Home</Link>
        </S.ListItem>
        <S.ListItem>
          <Link to="/projects" activeClassName="active-menu">Projects</Link>
        </S.ListItem>
        <S.ListItem>
          <Link to="/articles" activeClassName="active-menu">Articles</Link>
        </S.ListItem>
        <S.ListItem>
          <a
            href="mailto:guilhermeorcezi@gmail.com"
          >
            Contact
          </a>
        </S.ListItem>
      </S.ListWrapper>
    </S.NavWrapper>
  </S.MenuWrapper>
)

export default Menu
