import styled from "styled-components"

export const SidebarWrapper = styled.aside`
  padding-bottom: 6px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 24px;
  background-color: rgb(52, 52, 52);
  bottom: 0px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  left: 0px;
  position: fixed;
  top: 0px;
  width: 260px;
  z-index: 500;

  @media screen and (min-width: 320px) {
    padding-top: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-right: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-left: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-bottom: calc(6px + (2 * (100vw - 320px)) / 1120);
  }

  @media (max-width: 767px) {
    display: none;
  }
`
