import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import * as S from "./styled"

const Title = () => {
  const {
    site: {
      siteMetadata: { position, pagename },
    },
  } = useStaticQuery(graphql`
    query MySiteMetadata {
      site {
        siteMetadata {
          title
          pagename
          description
          author
          position
        }
      }
    }
  `)

  return (
    <S.TitleWrapper>
      <h1>{pagename}</h1>
      <p className="description">{position}</p>
      <p className="description student">Systems Analysis and Development student</p>
    </S.TitleWrapper>
  )
}

export default Title
