import styled from "styled-components"

export const FooterWrapper = styled.footer`
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 48px;
  padding-bottom: 48px;
  font-size: 13px;
  line-height: 1.6em;
  background-color: rgb(52, 52, 52);
  text-align: center;

  @media screen and (min-width: 320px) {
    font-size: calc(13px + (2 * (100vw - 320px)) / 1120);
    padding-bottom: calc(48px + (16 * (100vw - 320px)) / 1120);
    padding-top: calc(48px + (16 * (100vw - 320px)) / 1120);
    padding-right: calc(24px + (8 * (100vw - 320px)) / 1120);
    padding-left: calc(24px + (8 * (100vw - 320px)) / 1120);
  }

  @media screen and (min-width: 767px) {
    display: none;
  }
`

export const TextBlock = styled.div`
  font-size: 13px;
  line-height: 1.6em;
  font-family: Rubik, Arial, sans-serif;

  @media screen and (min-width: 320px) {
    font-size: calc(13px + (2 * (100vw - 320px)) / 1120);
  }

  h1 {
    font-size: 18px;
    line-height: 1.6em;
    display: block;
    font-family: Rubik, Arial, sans-serif;
    font-weight: 500;
    word-spacing: -0.04em;
    color: rgb(237, 237, 237);
  }

  @media screen and (min-width: 320px) {
    h1 {
      margin-bottom: calc(24px + (8 * (100vw - 320px)) / 1120);
      margin-bottom: 24px;
      font-size: calc(18px + (8 * (100vw - 320px)) / 1120);
    }
  }

  p {
    font-size: 15px;
    line-height: 1.6em;
    font-family: Rubik, Arial, sans-serif;
  }

  @media screen and (min-width: 320px) {
    p {
      font-size: calc(15px + (3 * (100vw - 320px)) / 1120);
    }
  }
`

export const Separator = styled.hr`
  height: 0px;
  clear: both;
  position: relative;
  width: 100%;
  margin-top: 12px;
  margin-bottom: 12px;
  border-color: transparent;
  border-width: 0px 0px 2px;
  background: transparent;

  @media screen and (min-width: 320px) {
    margin-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
    margin-top: calc(12px + (4 * (100vw - 320px)) / 1120);
  }

  @media (max-width: 767px) {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
`

export const linkEmail = styled.a`
  padding-top: 3px;
  font-weight: 500;
  letter-spacing: 0.08em;
  line-height: 1.4em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: rgb(237, 237, 237);
  font-family: Rubik, Arial, sans-serif;
  border-style: solid;
  border-width: 0px 0px 2px;
  transition: border-color 0.3s ease 0s, color 0.3s ease 0s;
  border-color: rgb(237, 237, 237);

  @media screen and (min-width: 320px) {
    padding-bottom: calc(3px + (1 * (100vw - 320px)) / 1120);
    padding-top: calc(3px + (1 * (100vw - 320px)) / 1120);
  }

  span,
  .emailLink {
    direction: rtl;
    unicode-bidi: bidi-override;
    color: rgb(237, 237, 237);
    text-decoration: none;
  }
`

export const SocialLinks = styled.ul`
  @media screen and (min-width: 320px) {
    margin-bottom: calc(12px + (4 * (100vw - 320px)) / 1120);
  }
`

export const LinkItem = styled.li`
  margin-left: 6px;
  margin-right: 6px;
  display: inline;

  @media screen and (min-width: 320px) {
    li:first-child {
      margin-left: calc(0px + (0 * (100vw - 320px)) / 1120);
      margin-left: 0px;
    }
  }

  a {
    color: rgb(237, 237, 237) !important;
    font-size: 18px;
  }
`
export const AuthorCredit = styled.div`
margin-top: calc(24px + (8 * (100vw - 320px)) / 1120);

svg{
  margin-top:1px;
}
`